//该文件专门用于创建整个应用的路由器
import VueRouter from "vue-router";
// 进入组件
import About from '../pages/About'
import Home from '../pages/Home'
import News from '../pages/News'
import Message from '../pages/Message'
import Detail from '../pages/Detail'

// 创建并暴露一个路由器
const router = new VueRouter({
    mode:'history',//默认是hash //哈希值 history
    routes: [
        {
            name: 'guanyu',
            path: '/about',
            component: About,
            meta: { isAuth: true,title: '关于' }
        },
        {
            name: 'zhuye',
            path: '/home',
            component: Home,
            meta: { title: '主页' },
            children: [
                {
                    name: 'xinwen',
                    path: 'news',
                    component: News,
                    // 路由元信息
                    meta: { isAuth: true, title: '新闻' },
                    /* beforeEnter: (to, from, next) => {
                        console.log('前置路由守卫', to, from);
                        if (to.meta.isAuth) {//判断是否需要鉴权
                            if (localStorage.getItem('school') === 'atguigu') {
                                next()
                            } else {
                                alert('学习名不对')
                            }
                        } else {
                            next()
                        }
                    } */
                    
                },
                {
                    name: 'xiaoxi',
                    path: 'message',
                    component: Message,
                    meta: { isAuth: true, title: '详情' },
                    children: [
                        {
                            name: 'xiangqing',
                            path: 'detail/:id/:title',
                            component: Detail,
                            // props的第一种写法 值为对象 该对象中的所有key-value 都会以props的形式，传给Detail组件
                            // props:{a:'1',b:'hello'}

                            // props的第二种写法 值为布尔值 若布尔值为真，就会把该路由组件收到的所有params参数，以props的形式，传给detail组件
                            // props:true

                            // props第三种方法 值为函数
                            props($route) {
                                return {
                                    id: $route.query.id,
                                    title: $route.query.title
                                }
                            }
                        }
                    ]
                }
            ]
        }
    ]
})
/* // 全局前置路由守卫---每次初始化的时候被调用，每次路由切换之前被调用
router.beforeEach((to, from, next) => {
    console.log('前置路由守卫',to, from);
    if (to.meta.isAuth) {//判断是否需要鉴权
        if (localStorage.getItem('school') === 'atguigu') {
            next()
        } else {
            alert('学习名不对')
        }
    } else {
        next()
    }
})*/
// 全局前置路由守卫---每次初始化的时候被调用，每次路由切换之后被调用
router.afterEach((to, from)=>{
    console.log('后置路由守卫',to,from);
    document.title= to.meta.title || '硅谷系统'
}) 

export default router